import React, { useState, useEffect } from "react";

const OptionCard = ({ title, img, menuSelected, setMenuSelected }) => {
  const [enabled, setEnabled] = useState(true);
  const category = title.toLowerCase();
  useEffect(() => {
    console.log(menuSelected);
    console.log(category);
  }, [menuSelected]);
  const new2 = {};
  new2[category] = !menuSelected[category];
  return (
    <div
      style={{ position: "relative" }}
      onClick={() => {
        setEnabled(!enabled);
        setMenuSelected({ ...menuSelected, ...new2 });
      }}
    >
      <div
        style={{
          backgroundColor: "#bf5830",
          backgroundImage: `url(${img}) `,
          backgroundSize: "cover",
          padding: "2rem",
          borderRadius: "5px",
          width: "10rem",
          height: "4rem",
          margin: "0rem 0.2rem",
          boxShadow: "2px 2px 2px 1px rgba(0, 0, 0, 0.2)",
          color: "white",
          filter: enabled ? "" : "blur(2px) grayscale(100%)",
          top: 0,
          left: 0,
        }}
      ></div>
      <h3
        style={{
          position: "absolute",
          bottom: "0.5rem",
          color: "white",
          left: 0,
          right: 0,
          margin: "auto",
        }}
      >
        {title}
      </h3>
    </div>
  );
};

export default OptionCard;
