import React, { useState, useEffect } from "react";

const CardSection = ({
  position,
  image,
  title,
  description,
  menuSelected,
  category,
}) => {
  const [selected, setSelected] = useState();

  return (
    <>
      {menuSelected[category] && (
        <div>
          <div
            className={
              selected ? "imgIcon enableCircle" : "imgIcon not-enableCircle"
            }
            style={{
              zIndex: selected ? 10 : 0,

              backgroundImage: `url(${image})`,
              backgroundSize: "cover",
              width: "5rem",
              height: "5rem",
              position: "absolute",
              top: `${position.top}%`,
              left: `${position.left}%`,
              borderRadius: "50%",
              boxShadow: "2px 2px 2px 1px rgba(0, 0, 0, 0.2)",
            }}
            onClick={() => {
              setSelected(!selected);
            }}
          />
          <div
            className={selected ? "enable" : "not-enable"}
            style={{
              zIndex: selected ? 9 : -1,
              backgroundColor: "#ffffffa6",
              width: "12rem",
              position: "absolute",
              top: `${position.top}%`,
              left: `${position.left + 4}%`,
              height: "5rem",
              borderRadius: "3% 3% 3% 3%",
              boxShadow: "2px 2px 2px 1px rgba(0, 0, 0, 0.2)",
            }}
          >
            <h3 style={{ margin: ".5rem 0rem", paddingLeft: "1rem" }}>
              {title}
            </h3>
            <p>{description}</p>
          </div>
        </div>
      )}
    </>
  );
};

export default CardSection;
